import React from 'react'
import styled from 'styled-components';

const index = () => {
  return (
    <Section>
        <Container>
            <div className=''>
                <Title style={{color: "#fff"}}>Bangladesh Open University</Title>
            </div>
        </Container>
    </Section>
  )
}

export default index;

export const Section = styled.section`
    width: 100%;
    height: calc(100vh - 70px);
    margin-top: 68px;
    overflow-x: hidden;
`

export const Container = styled.div`
    width: 100%;
    min-height: calc(100vh - 70px);
    background: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)),url(${require("../components/images/hero.jpg")});
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    padding-left: 4rem;
    @media (max-width: 768px){
        padding-left: 0;
        justify-content: center;
    }
`

export const Title = styled.h1`
    @media (max-width: 768px){
        font-size: 1.4rem;
        text-align: center;
    }
`